.App {
  text-align: center;
}


.logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 30px;
  }
  
  .header {
    background-color: #222;
    height: 50px;
    padding: 20px;
    color: white;
  }
  
  .header span{
    margin-right: 20px;
    font-size: 20px;
  }
  
  .intro {
    font-size: large;
  }
  
  span.author{
    font-size: small;
    color: rgb(179, 179, 179);
  }
  .author a{
    color: rgb(179, 179, 179);
  }
  
  @-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
.testing-colors{
    color:  #2a2b83 #ac4a59 #5cb5d8 #61f574 #f17341;
    color: #E8895B #A2DA3D, #EFF4C3, #10101C;
    color: #E8895B #A2DA3D, , #38397C, #10101C, 
}

.Axis path,
.Axis line {
  stroke: #E0E0E0;
}

.Axis-Band line,
.Axis-Band path,
.Axis-Bottom line,
.Axis-Bottom path {
  stroke-opacity: 0 ;
}


.Axis text {
  font-size: 11px;
  fill: black;
}

.Axis-Bottom text{
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  text-anchor: end;
  fill: none;
}



body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

